import React, { useState } from 'react'
import Modal from 'react-modal'
import ShoItemReduxForm from '../ShopItems/ShopItemForm'
import i from './Modal.module.css'

let ModalWindow = (props) => {
	const [modal, setModal] = useState(false)

	let openModal = () => {
		setModal(true)
	}

	let closeModal = () => {
		setModal(false)
	}

	const customStyles = {
		content: {
			top: '35%',
			left: '50%',
			right: 'auto',
			bottom: 'auto',
			marginRight: '-50%',
			width: '20%',
			transform: 'translate(-40%, -10%)',
		},
	}

	return (
		<div>
			<div className={i.newItem}>
				<img
					onClick={openModal}
					src='https://png.pngtree.com/element_our/sm/20180516/sm_5afbe35fd36cc.jpg'
					alt='logo'
				/>
			</div>

			<Modal style={customStyles} isOpen={modal} onRequestClose={closeModal}>
				<ShoItemReduxForm onSubmit={props.onSubmit} closeModal={closeModal} />
			</Modal>
		</div>
	)
}

export default ModalWindow
