import ShopItems from './ShopItems'
import {connect} from 'react-redux'
import { addNewItem, deleteItem, sortItems } from '../../Redux/shopItemReducer'

const mapStateToProps = (state) =>{
    return{
        goods: state.shopPage.goods,
        isAuth: state.auth.isAuth
    }
}
const ShopItemsContainer = connect(mapStateToProps, { addNewItem, deleteItem, sortItems})(ShopItems)
export default ShopItemsContainer