let shopItemReducer = (state = initialState, action) => {
	switch (action.type) {
		case 'AddItem': {
			let newWare = action.ware
			return {
				goods: [...state.goods, { ...newWare, id: state.goods.length + 1}],
			}
		}
		case 'DeleteItem': {
			return {
				goods: [...state.goods.filter((goods) => goods.id !== action.id)],
			}
		}

		case 'SortItems': {
			return {
				goods: [
					...state.goods.slice().sort((a, b) => {
						if (action.sortBy === 'start_date') {
							return a.id > b.id ? 1 : -1
						} else if (action.sortBy === 'name') {
							return a.name.toLocaleLowerCase() > b.name.toLocaleLowerCase() ? 1 : -1
						} else if (action.sortBy === 'count') {
							return Number(a.count) > Number(b.count) ? 1 : -1
						}
					})
				]
			}
		}
		default:
			return state
	}
}

export const addNewItem = (ware) => ({ type: 'AddItem', ware })

export const deleteItem = (id) => ({ type: 'DeleteItem', id })

export const sortItems = (sortBy) => ({ type: 'SortItems', sortBy })

export default shopItemReducer
