import React from 'react'
import i from './Item.module.css'

const Item = ({id, src, name, size, count, weight, comments, deleteItem, isAuth }) => {
	return (
		<div className={i.flexItm}  >
			{isAuth ? <img onClick={()=> {deleteItem(id)}} className={i.img}  src="https://findicons.com/files/icons/1262/amora/256/delete.png" alt="delete" /> : ''}
			<img className={i.photo} src={src} alt='photo' /> 
			<div>{name} </div>
			<div>{count}</div>
			<div>{size}</div>
			123$
			<div>{weight}</div>
			<div>{comments} </div>		
		</div>
	)
}

export default Item
