import React from 'react'
import { Field, reduxForm } from 'redux-form'
import { Input } from '../../common/FormControl/FormControl'

import { required } from '../../common/validators/validator'

const SimpleForm = ({
	handleSubmit,
	pristine,
	reset,
	submitting,
	closeModal,
}) => {
	return (
		<form onSubmit={handleSubmit}>
			<div>
				<label>Name</label>
				<div>
					<Field
						name='name'
						component={Input}
						type='text'
						placeholder='Name'
						validate={[required]}
					/>
				</div>
			</div>
			<div>
				<label>Link to img</label>
				<div>
					<Field
						name='src'
						component={Input}
						type='text'
						placeholder='Link to img'
						validate={[required]}
					/>
				</div>
			</div>
			<div>
				<label>Count</label>
				<div>
					<Field
						name='count'
						component={Input}
						type='text'
						placeholder='Count'
						validate={[required]}
					/>
				</div>
			</div>
			<div>
				<label>Size</label>
				<div>
					<Field
						name='size'
						component={Input}
						type='text'
						placeholder='Size'
						validate={[required]}
					/>
				</div>
			</div>
			<div>
				<label>Weight</label>
				<div>
					<Field
						name='weight'
						component={Input}
						type='text'
						placeholder='Weight'
						validate={[required]}
					/>
				</div>
			</div>
			<div>
				<label>Comments</label>
				<div>
					<Field
						name='comments'
						component={Input}
						type='text'
						placeholder='Comments'
						validate={[required]}
					/>
				</div>
			</div>
			<div>
				<button type='submit' disabled={pristine || submitting}>
					Submit
				</button>
				<button type='button' disabled={pristine || submitting} onClick={reset}>
					Clear Values
				</button>
				<button onClick={closeModal}>Close</button>
			</div>
		</form>
	)
}

let ShoItemReduxForm = reduxForm({
	form: 'add-shop-item',
})(SimpleForm)

export default ShoItemReduxForm
