let initialState = {
    isAuth:false
}

let shopItemReducer = (state = initialState, action) => {
	switch (action.type) {
		case 'auth': {
			return{
				isAuth: !state.isAuth
			}
		}
		default:
			return state
	}
}

export const auth = () => ({type: 'auth'})

export default shopItemReducer